<template>
  <div>
    <div id="myChart" :style="{ height: '1400px' }"></div>
  </div>
</template>

<script>
import storejs from "storejs";
export default {
  props: {
    msg: String,
  },

  mounted() {
    // 基于准备好的dom，初始化echarts实例
    var myChart = this.$echarts.init(document.getElementById("myChart"));

    let healthData = storejs.get("health");
    let xDatas = healthData.datas.map((e) => e.date).reverse();

    let seriesDatas = [];
    let datas = healthData.datas.reverse();
    for (let i = 0; i < healthData.names.length; i++) {
      let data = datas.map((e) => e.data[i]);
      seriesDatas.push({
        name: healthData.names[i],
        type: "line",
        data: data,
      });
    }

    // 指定图表的配置项和数据
    var option = {
      title: {
        text: "身高cm",
      },
      tooltip: {
        trigger: "axis",
      },
      legend: {
        data: healthData.names,
      },
      grid: {
        left: "3%",
        right: "4%",
        bottom: "3%",
        containLabel: true,
      },
      toolbox: {
        feature: {
          saveAsImage: {},
        },
      },
      xAxis: {
        type: "category",
        boundaryGap: false,
        data: xDatas,
      },
      yAxis: {
        type: "value",
        min: 50,
        max: 170,
        splitNumber: 130,
      },
      series: seriesDatas,
    };
    // 使用刚指定的配置项和数据显示图表。
    myChart.setOption(option);
  },
  methods: {},
};
</script>

